<template>
  <div class="addressList">
    <van-nav-bar title="地址管理" left-text="返回" @click-left="goBack" fixed />
    <van-address-list
      v-model="chosenAddressId"
      :list="addressMsg"
      default-tag-text="默认"
      @add="goAddress"
      @edit="goAddress"
      class="content"
    />
  </div>
</template>

<script>
import "../assets/css/addressList.less"
export default {
  data() {
    return {
      chosenAddressId: "1",
      addressMsg: [
      ],
    };
  },
  created() {
    this.getAddress();
  },
  methods: {
    goAddress(address) {
      let obj = {
          name:"Address"
      };
      if(address){
          obj.params = {
              aid:address.id
          }
      }
      this.$router.push(obj);
    },
    goBack() {
      this.$router.go(-1);
    },
    // 获取地址
    getAddress() {
      let tkStr = localStorage.getItem("__Tk");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        // 延迟关闭加载框时间 为0则不自动关闭
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString: tkStr
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 20000) {
            result.data.result.forEach(val => {
              let data = {
                id: val.aid,
                name: val.name,
                tel: val.tel,
                address:
                  val.province + val.city + val.county + val.addressDetail,
                isDefault: val.isDefault
              };
              if(val.isDefault){
                  this.chosenAddressId = val.isDefault
              }

              this.addressMsg.push(data);
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
  }
};
</script>

<style lang="less" scoped>

</style>